import Mouse from "./Mouse";

const MouseRender = (props) => {
  return (
    <Mouse
      render={(point) => {
        const { x, y } = point;
        return (
          <div>
            <div>x: {x}</div>
            <div>y: {y}</div>
          </div>
        );
      }}
    />
  );
};

export default MouseRender;
